import {createRouter, createWebHistory} from 'vue-router';
import Login from './components/UserLogin.vue';
import Dashboard from './components/UserDashboard.vue';
import OciHome from './components/OciHome.vue';
import OciUser from './components/OciUser.vue';
import OciCreateInstance from './components/OciCreateInstance.vue';
import OciInstanceInfo from './components/OciInstanceInfo.vue';
import CreateTask from './components/OciCreateTask.vue';
import OciCreateInstanceBatch from './components/OciCreateInstanceBatch.vue';
import OciLog from './components/OciLog.vue';

const routes = [
    {path: '/', redirect: '/login'},
    {path: '/login', component: Login},
    {
        path: '/dashboard',
        component: Dashboard,
        meta: {requiresAuth: true},
        children: [
            {path: '', component: OciHome}, // 默认页面
            {path: 'home', component: OciHome},
            {path: 'user', component: OciUser},
            {path: "createTask", component: CreateTask},
            {path: "ociLog", component: OciLog},
            {path: "sysCfg", component: () => import('@/components/OciSysCfg.vue')},
            {path: 'createInstance', component: OciCreateInstance},
            {path: 'ociInstanceInfo', component: OciInstanceInfo},
            {path: 'ociUpdateInstanceCfg', component: () => import('@/components/OciUpdateInstanceCfg')},
            {path: "ociCreateInstanceBatch", component: OciCreateInstanceBatch},
        ]
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes
});

// 路由守卫 - 全局拦截
router.beforeEach((to, from, next) => {
    const isAuthenticated = sessionStorage.getItem('token');

    if (to.matched.some(record => record.meta.requiresAuth) && !isAuthenticated) {
        // 如果页面需要登录且用户未登录，重定向到登录页
        next('/login');
    } else {
        if (window.ws) {
            console.log('Closing existing WebSocket connection before route change.');
            window.ws.close();
            window.ws = null;
        }
        next(); // 允许进入页面
    }
});

export default router;
