<template>
  <div>
    <!-- 顶部卡片 -->
    <el-row :gutter="20" class="card-container" v-loading="loading">
      <el-col :span="6">
        <div class="data-card">
          <h3>{{ '失效API数量 / 总API数量' }}</h3>
          <p>{{ cards.users }}</p>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="data-card">
          <h3>{{ '开机任务数量' }}</h3>
          <p>{{ cards.tasks }}</p>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="data-card">
          <h3>{{ '区域数量' }}</h3>
          <p>{{ cards.regions }}</p>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="data-card">
          <h3>{{ '系统运行天数' }}</h3>
          <p>{{ cards.days }}</p>
        </div>
      </el-col>
    </el-row>

    <!-- 图表区域 -->
    <el-row :gutter="20" class="chart-container">
      <el-col :span="12">
        <!-- 左侧饼图 -->
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="chart" ref="cpuChart"></div>
          </el-col>
          <el-col :span="12">
            <div class="chart" ref="memoryChart"></div>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="12">
        <!-- 右侧折线图 -->
        <div class="chart" ref="trafficChart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import * as echarts from 'echarts';

export default {
  data() {
    return {
      loading: false,
      cards: {},
      cpuChart: null,
      memoryChart: null,
      trafficChart: null,
      ws: null,
      reconnectAttempts: 0,
      maxReconnectAttempts: 5,
      reconnectDelay: 5000,
      manualClose: false,
      isReconnecting: false,
    };
  },
  mounted() {
    this.initInfo();
    this.initCharts();
    this.initWebSocket();
    // 添加路由守卫监听
    this.$router.beforeEach((to, from, next) => {
      this.closeWebSocket();
      next();
    });
  },
  beforeUnmount() {
    this.closeWebSocket();
  },
  methods: {
    initInfo() {
      this.loading = true
      this.$axios.get('/sys/glance')
          .then(response => {
            this.cards = response.data.data
            this.loading = false
          })
          .catch(error => {
            console.error("Error fetching glance data:", error);
            throw error;
          })
    },
    initCharts() {
      this.cpuChart = echarts.init(this.$refs.cpuChart);
      this.memoryChart = echarts.init(this.$refs.memoryChart);
      this.trafficChart = echarts.init(this.$refs.trafficChart);

      this.cpuChart.setOption({
        title: {text: 'CPU 使用率（%）', left: 'center'},
        tooltip: {trigger: 'item'},
        series: [{
          name: 'CPU 使用率（%）',
          type: 'pie',
          radius: '50%',
          data: [],
        }],
      });

      this.memoryChart.setOption({
        title: {text: 'Memory 使用率（%）', left: 'center'},
        tooltip: {trigger: 'item'},
        series: [{
          name: 'Memory 使用率（%）',
          type: 'pie',
          radius: '50%',
          data: [],
        }],
      });

      this.trafficChart.setOption({
        title: {text: '流量实时速率（KB/s）', left: 'center'},
        tooltip: {
          trigger: 'axis',
          formatter: function (params) {
            let result = params[0].axisValue + '<br/>';
            params.forEach(param => {
              // 添加每个系列的数据
              result += `${param.seriesName}: ${param.value} KB/s<br/>`;
            });
            return result;
          }
        },
        legend: {  // 添加图例
          data: ['入站', '出站'],
          bottom: 10
        },
        xAxis: {
          type: 'category',
          data: [],
          axisLabel: {
            rotate: 30  // 旋转标签避免重叠
          }
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            formatter: '{value} KB/s'
          }
        },
        series: [
          {
            name: '入站',
            type: 'line',
            data: [],
            smooth: true,  // 平滑曲线
            showSymbol: true  // 显示数据点
          },
          {
            name: '出站',
            type: 'line',
            data: [],
            smooth: true,
            showSymbol: true
          }
        ]
      });
    },

    async closeWebSocket() {
      if (this.ws) {
        if (this.ws.readyState === WebSocket.OPEN) {
          this.ws.close();
          // 等待连接完全关闭
          await new Promise(resolve => {
            this.ws.onclose = () => resolve();
          });
        }
        this.ws = null;
        this.reconnectAttempts = 0;
        this.isReconnecting = false;
      }
    },

    initWebSocket() {
      try {
        const token = sessionStorage.getItem('token');
        const baseURL = this.$axios.defaults.baseURL.replace(/^http/, 'ws').replaceAll('/api', '');
        const wsURL = `${baseURL}/metrics/${token}`;

        this.ws = new WebSocket(wsURL);

        this.ws.onopen = () => {
          console.log('WebSocket connection established.');
          this.reconnectAttempts = 0;
        };

        this.ws.onmessage = (event) => {
          try {
            const {cpuUsage, memoryUsage, trafficData} = JSON.parse(event.data);
            this.updateCharts(cpuUsage, memoryUsage, trafficData);
          } catch (error) {
            console.error('Error parsing message:', error);
          }
        };

        this.ws.onclose = (event) => {
          console.log('WebSocket connection closed:', event);
        };

        this.ws.onerror = (error) => {
          console.error('WebSocket error:', error);
        };
      } catch (error) {
        console.error('Error initializing WebSocket:', error);
      }
    },

    updateCharts(cpuUsage, memoryUsage, trafficData) {
      // 将图表更新逻辑抽离出来
      if (this.cpuChart) {
        this.cpuChart.setOption({
          series: [{
            data: [
              {value: cpuUsage.used, name: '已使用'},
              {value: cpuUsage.free, name: '空闲'},
            ]
          }],
        });
      }

      if (this.memoryChart) {
        this.memoryChart.setOption({
          series: [{
            data: [
              {value: memoryUsage.used, name: '已使用'},
              {value: memoryUsage.free, name: '空闲'},
            ]
          }],
        });
      }

      if (this.trafficChart) {
        this.trafficChart.setOption({
          xAxis: {data: trafficData.timestamps},
          series: [
            {name: '入站', data: trafficData.inbound},
            {name: '出站', data: trafficData.outbound},
          ],
        });
      }
    }
  }
};
</script>

<style>
.card-container {
  margin-bottom: 30px;
}

.data-card {
  background: #f5f5f5;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.data-card h3 {
  margin: 0;
  font-size: 16px;
  color: #888;
}

.data-card p {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

.chart-container {
  display: flex;
  flex-wrap: wrap;
}

.chart {
  width: 100%;
  height: 500px;
  margin-bottom: 20px;
  background: #fff;
  border: 1px solid #ebebeb;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}
</style>
