<template>
  <el-button @click="goBack">返回配置页面</el-button>
  <span style="margin-left: 20px;"><el-text type="primary">配置：{{ this.usernameList }} 批量新增开机任务</el-text></span>
  <el-divider/>
  <el-form :model="createInstanceBatchParams.instanceInfo" label-width="auto" style="max-width: 600px">
    <el-form-item label="开机数量：">
      <el-input-number v-model="createInstanceBatchParams.instanceInfo.createNumbers" :min="1" :max="99999">
        <template #suffix>
          <span>台</span>
        </template>
      </el-input-number>
    </el-form-item>
    <el-form-item label="时间间隔：">
      <el-input-number v-model="createInstanceBatchParams.instanceInfo.interval" :min="1" :max="99999">
        <template #suffix>
          <span>秒</span>
        </template>
      </el-input-number>
    </el-form-item>
    <el-form-item label="CPU：">
      <el-input-number v-model="createInstanceBatchParams.instanceInfo.ocpus" :min="1" :max="99999">
        <template #suffix>
          <span>核</span>
        </template>
      </el-input-number>
    </el-form-item>
    <el-form-item label="内存：">
      <el-input-number v-model="createInstanceBatchParams.instanceInfo.memory" :min="1" :max="99999">
        <template #suffix>
          <span>GB</span>
        </template>
      </el-input-number>
    </el-form-item>
    <el-form-item label="磁盘空间：">
      <el-input-number v-model="createInstanceBatchParams.instanceInfo.disk" :min="50" :max="99999">
        <template #suffix>
          <span>GB</span>
        </template>
      </el-input-number>
    </el-form-item>
    <el-form-item label="系统架构：">
      <el-radio-group v-model="createInstanceBatchParams.instanceInfo.architecture">
        <el-radio value="ARM">ARM</el-radio>
        <el-radio value="AMD">AMD</el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="系统类型：">
      <el-radio-group v-model="createInstanceBatchParams.instanceInfo.operationSystem">
        <el-radio value="Canonical Ubuntu">Canonical Ubuntu</el-radio>
        <el-radio value="Oracle Autonomous Linux">Oracle Autonomous Linux</el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="root密码：" style="width: 70%">
      <el-input placeholder="请输入root密码" v-model="createInstanceBatchParams.instanceInfo.rootPassword" clearable>
        <template #append>
          <el-button type="text" @click="generatePassword" :icon="Search">随机生成</el-button>
        </template>
      </el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit" :loading="loading">开机</el-button>
      <el-button @click="goBack">取消</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import {ElButton, ElMessage} from 'element-plus';

export default {
  components: {
    ElButton
  },
  data() {
    return {
      loading: false,
      users: [],
      usernameList: [],
      createInstanceBatchParams: {
        userIds: [],
        instanceInfo: {
          ocpus: 1,
          memory: 1,
          disk: 50,
          architecture: 'ARM',
          interval: 60,
          createNumbers: 1,
          operationSystem: 'Canonical Ubuntu',
          rootPassword: '',
        }
      },
    }
  },
  mounted() {
    this.users = JSON.parse(this.$route.query.row || '{}')
    this.usernameList = this.users.map(user => user.username);
    this.createInstanceBatchParams.userIds = this.users.map(user => user.id);
  },
  methods: {
    goBack() {
      this.$router.back(); // 返回上一个页面
    },
    generatePassword() {
      const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
      let password = '';
      for (let i = 0; i < 8; i++) {
        password += chars.charAt(Math.floor(Math.random() * chars.length));
      }
      this.createInstanceBatchParams.instanceInfo.rootPassword = password;
    },
    onSubmit() {
      this.loading = true;
      this.$axios.post('/oci/createInstanceBatch', {...this.createInstanceBatchParams})
          .then(response => {
            if (response.data.success) {
              this.loading = false;
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.goBack()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    }
  }
}
</script>

