<template>
  <div>
    <el-button type="success"
               style="margin-bottom: 20px;"
               @click="createTaskPage">
      刷新
    </el-button>

    <el-input
        v-model="createTaskPageParams.keyword"
        placeholder="模糊搜索配置名称/区域"
        @input="createTaskPage"
        clearable prefix-icon="Search"
        style="margin-left: 20px; width: 200px; margin-bottom: 20px;"
    />

    <el-select
        v-model="createTaskPageParams.architecture"
        @change="createTaskPage"
        placeholder="系统架构筛选"
        style="margin-left: 20px; width: 180px; margin-bottom: 20px;"
    >
      <el-option label="全部" :value="null"></el-option>
      <el-option label="ARM" :value="'ARM'"></el-option>
      <el-option label="AMD" :value="'AMD'"></el-option>
    </el-select>

    <el-button
        type="danger"
        @click="handleStopCreateBatch"
        :disabled="!stopCreateBatchIds.length"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      批量停止
    </el-button>

    <!-- 可滑动的表格 -->
    <div style="overflow-x: auto;">
      <el-table
          :data="createTaskRsp"
          style="width: 100%; min-width: 800px;"
          v-loading="loading"
          @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="35"/>
        <el-table-column prop="username" label="名称"/>
        <el-table-column label="区域">
          <template #default="{ row }">
            <el-tag type="primary">
              {{ row.region }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="ocpus" label="CPU（核数）"/>
        <el-table-column prop="memory" label="内存（GB）"/>
        <el-table-column prop="disk" label="硬盘（GB）"/>
        <el-table-column prop="operationSystem" label="操作系统"/>
        <el-table-column label="系统架构">
          <template #default="{ row }">
            <el-tag :type="row.architecture === 'ARM' ? 'success':'info'">
              {{ row.architecture }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="interval" label="间隔（秒）"/>
        <el-table-column label="数量">
          <template #default="{ row }">
            <el-tag type="primary">
              {{ row.createNumbers }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="次数" sortable>
          <template #default="{ row }">
            <el-tag type="warning">
              {{ row.counts }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column sortable prop="createTime" label="创建时间">
          <template #default="{ row }">
            <el-tag type="info">
              {{ row.createTime }}
            </el-tag>
          </template>
        </el-table-column>
        <!-- 缩小的操作列 -->
        <el-table-column fixed="right" label="操作" width="100">
          <template #default="{ row }">
            <el-button @click="handleStopTask(row)" type="danger" link>停止</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页控制 -->
    <el-pagination
        @current-change="handlePageChange"
        @size-change="handleSizeChange"
        v-model:current-page="createTaskPageParams.currentPage"
        v-model:page-size="createTaskPageParams.pageSize"
        :page-sizes="[5, 10, 20, 50]"
        :total="total"
        layout="total, sizes, prev, pager, next, jumper"
        style="margin-top: 20px;"
    />
  </div>
</template>

<script>
import {ElTable, ElTableColumn, ElPagination, ElInput, ElButton, ElMessage} from 'element-plus';

export default {
  components: {
    ElTable, ElTableColumn, ElPagination, ElInput, ElButton
  },
  data() {
    return {
      createTaskPageParams: {
        keyword: '',
        currentPage: 1,
        pageSize: 5,
        architecture: ''
      },
      createTaskRsp: [],
      selectedTasks: [],
      total: 0,
      stopCreateBatchIds: [],
      loading: true,
    };
  },
  mounted() {
    this.createTaskPage();
  },
  methods: {
    handlePageChange(page) {
      this.createTaskPageParams.currentPage = page;
      this.createTaskPage();
    },
    handleSizeChange(size) {
      this.createTaskPageParams.currentPage = 1;
      this.createTaskPageParams.pageSize = size;
      this.createTaskPage();
    },
    handleSelectionChange(val) {
      this.stopCreateBatchIds = val;
    },
    createTaskPage() {
      this.loading = true;
      this.$axios.post('/oci/createTaskPage', {...this.createTaskPageParams})
          .then(response => {
            this.createTaskRsp = response.data.data.records;
            this.total = response.data.data.total;
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.loading = false;
    },
    handleStopTask(row) {
      const confirmed = window.confirm('确定要停止 ' + row.username + ' 的这个开机任务吗?');
      if (confirmed) {
        this.selectedTasks.push(row.id)
        this.$axios.post('/oci/stopCreateBatch', {idList: this.selectedTasks})
            .then(response => {
              if (response.data.success) {
                ElMessage({
                  message: response.data.msg,
                  type: 'success',
                  duration: 2000 // 设置提示显示时间，单位是毫秒
                });
              } else {
                ElMessage({
                  message: response.data.msg,
                  type: 'error',
                  duration: 2000 // 设置提示显示时间，单位是毫秒
                });
              }
              this.createTaskPage();
            })
            .catch(error => {
              console.error('Error:', error)
            })
      } else {
        this.$message.info('已取消');
      }
    },
    handleStopCreateBatch() {
      const confirmed = window.confirm('确定要停止这些开机任务吗');
      if (confirmed) {
        this.stopCreateBatch();
      } else {
        this.$message.info('已取消');
      }
    },
    stopCreateBatch() {
      const ids = this.stopCreateBatchIds.map(record => record.id);
      this.$axios.post('/oci/stopCreateBatch', {idList: ids})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.createTaskPage();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    }
  }
}
</script>

