<template>
  <div class="login-container">
    <el-card class="login-card">
      <h2 class="login-title">oci-helper Login</h2>
      <el-form @submit.prevent="login" @keydown.enter.prevent="login">
        <el-form-item label="" label-width="0%">
          <el-input v-model="loginParams.account" placeholder="请输入账号" clearable/>
        </el-form-item>
        <el-form-item label="" label-width="0%">
          <el-input show-password v-model="loginParams.password" placeholder="请输入密码" type="password" clearable/>
        </el-form-item>
        <el-form-item v-if="enableMfa">
          <el-input show-password v-model="loginParams.mfaCode" placeholder="请输入MFA验证码" clearable/>
        </el-form-item>
        <el-form-item style="text-align: center;" class="login-button-container">
            <el-button type="primary" @click="login" :loading="loading" class="login-button">Login</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <!-- Footer 放在登录框下面 -->
    <el-footer class="login-footer">
      <el-link href="https://github.com/Yohann0617" target="_blank">© Yohann All Rights Reserved</el-link>
    </el-footer>
  </div>
</template>

<script>
import {ElMessage} from "element-plus";

export default {
  data() {
    return {
      loginParams: {
        account: '',
        password: '',
        mfaCode: null
      },
      loading: false,
      enableMfa: false
    };
  },
  mounted() {
    this.getEnableMfa()
  },
  methods: {
    getEnableMfa() {
      this.$axios.post('/sys/getEnableMfa', {})
          .then(response => {
            if (response.data.success) {
              // ElMessage({
              //   message: response.data.msg,
              //   type: 'success',
              //   duration: 2000 // 设置提示显示时间，单位是毫秒
              // });
              this.enableMfa = response.data.data
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async login() {
      if (this.loginParams.account === 'yohann' && this.loginParams.password === 'yohann') {
        ElMessage({
          message: '该账号为默认账号，请修改并重启后再尝试登录',
          type: 'error',
          duration: 2000
        });
        return;
      }

      this.loading = true;
      try {
        const response = await this.$axios.post('/sys/login', {...this.loginParams});
        if (response.data.success) {
          ElMessage({
            message: response.data.msg,
            type: 'success',
            duration: 2000 // 设置提示显示时间，单位是毫秒
          });
          sessionStorage.setItem('token', response.data.data);
          setTimeout(() => {
            this.$router.push('/dashboard');
          }, 2000);
        } else {
          ElMessage({
            message: response.data.msg,
            type: 'error',
            duration: 2000 // 设置提示显示时间，单位是毫秒
          });
          this.loading = false;
        }
      } catch (error) {
        console.error('Error:', error);
        ElMessage({
          message: '登录失败，请重试',
          type: 'error',
          duration: 2000
        });
        this.loading = false;
      }
    }
  }
};
</script>

<style scoped>
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f0f2f5;
}

.login-card {
  width: 400px;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.login-title {
  text-align: center;
  margin-bottom: 40px;
  color: #304156;
}

.login-button-container {
  text-align: center;
  margin-left: -2%;
}

.login-button {
  width: 370px; /* 设置按钮宽度 */
  margin-top: 20px;
  color: white;
  font-weight: bold;
  border-radius: 25px;
}

.login-footer {
  text-align: center;
  font-size: 14px;
  margin-top: 20px;
  color: #304156;
}
</style>
